
import  * as actionType from '../action/action' 

import {updateObject} from '../utility'

const INGREDIENTSPRICE={
    salad:10,
    cheese:20,
    meat:40,
    bacon:30
};

const initialstate={
    ingredients:null,
    totalPrice:10,
    error:false,
    building:false
}


const reducer=(oldstate=initialstate,action)=>{

    switch(action.type){
   
        case (actionType.ADDINGREDIENT):

        const updatedIng={[action.ingredientName]:oldstate.ingredients[action.ingredientName] + 1 }
       const updatedIngredients=updateObject(oldstate.ingredients,updatedIng)
       const updatedState={
           ingredients:updatedIngredients,
           totalPrice:oldstate.totalPrice + INGREDIENTSPRICE[action.ingredientName],
           building:true
       }
        return updateObject(oldstate,updatedState)

    
        case (actionType.REMOVEINGREDIENT):
                const updatedIngvar={[action.ingredientName]:oldstate.ingredients[action.ingredientName] - 1 }
                const updatedIngredientsvar=updateObject(oldstate.ingredients,updatedIngvar)
                const updatedStatevar={
                    ingredients:updatedIngredientsvar,
                    totalPrice:oldstate.totalPrice + INGREDIENTSPRICE[action.ingredientName],
                    building:true
                }
                 return updateObject(oldstate,updatedStatevar)


            // return{

            //     ...oldstate,
            //     ingredients:{
            //         ...oldstate.ingredients,
            //         [action.ingredientName]:oldstate.ingredients[action.ingredientName] - 1
            //     },
            //     totalPrice:oldstate.totalPrice - INGREDIENTSPRICE[action.ingredientName]

            // }

        case (actionType.SETINGREDIENTS):

        const updatedItems={
            ingredients:{
                salad:action.ingredientsvar.salad,
                bacon:action.ingredientsvar.bacon,
                cheese:action.ingredientsvar.cheese,
                meat:action.ingredientsvar.meat,
            },
                error:false,
                totalPrice:10,
                building:false
        }
                return updateObject(oldstate, updatedItems)


            // return{
            //     ...oldstate,
            //     ingredients:{
            //         salad:action.ingredientsvar.salad,
            //         bacon:action.ingredientsvar.bacon,
            //         cheese:action.ingredientsvar.cheese,
            //         meat:action.ingredientsvar.meat,
            //     },
            //         error:false,
            //         totalPrice:10
            // }

        case (actionType.FAILEDTOFETCH):
            return updateObject(oldstate,{error:true})

            // return{
            //     ...oldstate,
            //     error:true
            // }    


         default:
             return oldstate;
           
    }
}











export default reducer; 


