import React, { Component } from 'react';
// import classes from './App.css';

import Layout from './hoc/Layout/Layout'

import BurgerBuilder from './containers/BurgerBuilder/BurgerBuilder';

import Checkout from '../src/containers/Checkout/Checkout'

import {Route} from 'react-router-dom';

// import OrderFromDB from './components/OrderFromDB/OrderDB'

// import AuthLogin from './containers/Auth/AuthSignup'

import Logout from './containers/Auth/Logout/Logout'

import * as action from './store/action/index';

import {withRouter} from 'react-router-dom'
import {connect} from 'react-redux'
import asynComponent from '../src/hoc/AsyncLazyLoding/asynccomponent' 
class App extends Component {
  componentDidMount(){
    this.props.onCheckLoginState();
  }

  render() {
    
    // let redirectAuth=(
    //   <Switch>
    //     <Route path='/login'  component={AuthLogin}/>
    //   <Route path='/' exact component={BurgerBuilder}/>
      
    //   </Switch>
    // )

    // if(this.props.isAuthenticated){
    //   console.log("isauth="+this.props.isAuthenticated)

    //   redirectAuth=(
    //         <div>
             
    //          <Route path='/checkout'  component={Checkout} />
    //          <Route path="/totalorders"  component={OrderFromDB}/>
    //          <Route path='/logout'  component={Logout}/>
    //          <Route path='/' exact component={BurgerBuilder}/>

    //         </div>

    //   )
    // }


    // const AsyncCheckout=asynComponent(()=>{
    //   return import('../src/containers/Checkout/Checkout')
    // })

    const  AsyncLogin = asynComponent(()=>{
      return import ('./containers/Auth/AuthSignup')
    })
    const  AsyncTotalOrder= asynComponent(()=>{
      return import ('./components/OrderFromDB/OrderDB')
    })

    return (
      <Layout isshowtotalorder={this.props.isAuthenticated}>
        {/* {redirectAuth} */}


        {/* <h1 className={classes.Head}>Burger Palace!!</h1> */}
          

      <Route path='/' exact component={BurgerBuilder}/>
      
      <Route path='/checkout' exact component={Checkout} />

      {/* <Route path='/checkout' exact component={AsyncCheckout} /> */}

      {/* <Route path='/login' exact component={AuthLogin}/> */}
      <Route path='/login' exact component={AsyncLogin}/>


      <Route path='/logout' exact component={Logout}/>


      {/* <Route path="/totalorders" exact component={OrderFromDB}/> */}
      <Route path="/totalorders" exact component={AsyncTotalOrder}/>



          {/* <BurgerBuilder/>
           <Checkout/> */}


        </Layout>
        
    );
  }
}

const mapToStateProps=(state)=>{
  return{
    isAuthenticated:state.auth.token!==null
  }
}

const mapDispatchToProps=(dispatch)=>{
  return{
    onCheckLoginState:()=>dispatch(action.checkLoginStateTime())
  };
};

export default withRouter(connect(mapToStateProps,mapDispatchToProps)(App));



