import React from 'react';
import image from '../../assets/Images/logo.png';
import classes from './Logo.css';

const Logo=(props)=>(

    <div className={classes.Logo} style={{height:props.height}}>
        <img src={image} alt="Myburger"></img>

    </div>

);

export default Logo;


