import * as actionType from './action'
import axios from '../../axios'
export const orderSucess=(id,orderData)=>{
    return {
        type:actionType.ORDERSUCCESS,
        orderId:id,
        orderData:orderData

    }
}


export const orderFail=(error)=>{
    return{
        type:actionType.ORDERFAILED,
        error:error
    }
}



export const orderPurchesStart=()=>{
    return{
        type:actionType.ORDER_PURCHES_START
    }
}

export const orderPurches=(token,orderData)=>{
    return dispatch=>{

        dispatch(orderPurchesStart())
console.log("TokenPost="+token)
        //  axios.post("/orders.json?auth="+token,orderData).then(response=>{
            axios.post("/orders.json",orderData).then(response=>{

            console.log("resdata="+response.data)
                dispatch(orderSucess(response.data.name,orderData))
            
                
        }).catch(error=>{
            dispatch(orderFail(error))
    
        });
        }
    }


export const purchesDone=()=>{
    return{
        type:actionType.PURCHES_DONE
    }
}

export const fetch_Data=(order)=>{
    return{
        type:actionType.FETCH_DATA_SUCCESS,
        order:order
    }
}

export const fetchDataFailed=(error)=>{
    return{
        type:actionType.FETCH_DATA_FAILED,
        error:error
    }
}


export const fetchDataStart=()=>{
    return{
        type:actionType.FETCH_DATA_START
    }
}


export  const fetchorder=(token,userId)=>{

       return dispatch=>{
            dispatch(fetchDataStart())
            console.log("Tokenget="+token)
        const queryParams=  'orderBy="userId"&equalTo="'+ userId + '"'
        // axios.get('/orders.json?auth='+token)
        axios.get('/orders.json?'+queryParams)
        .then(res=>{

            console.log(res.data)
            let fetchData=[];
            for(let key in res.data){
                fetchData.push({
                    ...res.data[key],
                    id:key
                });
            }
            
                dispatch(fetch_Data(fetchData))
            // this.setState({loading:false, order:fetchData})
        }).catch(err=>{
            dispatch(fetchDataFailed(err.response.data.error))

            // this.setState({loading:false})
        });

       }

}


