import axios from '../../axios';

import * as actionType from './action'

export const authStart=()=>{
    return{
        type:actionType.AUTH_START
    }
}


export const authSuccess=(userId,tokenId)=>{
    return{
        type:actionType.AUTH_SUCCESS,
        tokenId:tokenId,
        userId:userId
    }
}

export const authFail=(error)=>{
    return{
        type:actionType.AUTH_FAILED,
        error:error
    }
}

export const Logout=()=>{

    localStorage.clear()
    // localStorage.removeItem(localStorage.getItem("Token"))
    // localStorage.removeItem(localStorage.getItem("ExpiryDate"));
    // localStorage.removeItem(localStorage.getItem("LocalId"))

    return{
        type:actionType.AUTH_LOGOUT
    }
}


export const checkAuthTimeout=(expireTime)=>{
    return dispath=>{
        console.log("ExpireTime="+ expireTime)
        setTimeout(() => {
            dispath(Logout())
        }, 3600*100);
    }
}

export const authAsyncSucces=(email,password,isSignUp)=>{
    
    return dispath=>{
        dispath(authStart());
          const  authData={
                email:email,
                password:password,
                authToken:true
            };
           let url="https://identitytoolkit.googleapis.com/v1/accounts:signUp?key=AIzaSyDaMnjGrEhVdQ6MqPpGkmqU4VKT1ektAE0"
            if(!isSignUp){
              url="https://identitytoolkit.googleapis.com/v1/accounts:signInWithPassword?key=AIzaSyDaMnjGrEhVdQ6MqPpGkmqU4VKT1ektAE0";

            }
            
axios.post(url,authData)
            .then(response=>{
                // console.log(response.data)
                console.log(response)
                localStorage.setItem("Token",response.data.idToken);
                // let ExpiryDate=new Date(new Date()+ response.data.expires)
                // let ExpiryDate=3600*1000
                localStorage.setItem("ExpiryDateTime",3600*1000)

                localStorage.setItem('userId',response.data.localId)

                dispath(authSuccess(response.data.localId,response.data.idToken))
                // dispath(checkAuthTimeout(response.data.expiresIn))
                dispath(checkAuthTimeout((response.data.expires)+Date.now() ))
                console.log("localId="+response.data.localId)
            })
            .catch(err=>{
                console.log(err);
                dispath(authFail(err));
            })


    }
}


export const setAuthRedirect=(path)=>{
    return{
        type:actionType.SET_AUTH_REDIRECT,
        path:path
    }
}


export const checkLoginStateTime=()=>{
    return dispatch=>{
        const token=localStorage.getItem("Token");
        const userId=localStorage.getItem("userId");
        
        if(!token){
            dispatch(Logout());
            
        }else{
             const expiryTime=(localStorage.getItem("ExpiryDateTime"))
            // const expiryTime=parseInt(localStorage.getItem("ExpiryDateTime"));
            if(expiryTime<3600*100){
                dispatch(Logout())

            }else{

                dispatch(authSuccess(userId,token))
                // dispatch(checkAuthTimeout( (expiryTime.getTime()-new Date().getTime()))/1000 )
                // dispatch(checkAuthTimeout(expiryTime.getTime()-new Date().getTime ))
                dispatch(checkAuthTimeout(expiryTime))
            

            }
        }


    }
}


