import React,{Component} from 'react';
import CheckoutSummary from '../../components/Order/checkoutsummary';

import ContactDetails from './ContactDetails/contactDetails';
import {Route,Redirect} from 'react-router-dom';
import {connect} from 'react-redux';

// import * as actions from '../../store/action/index'

class Checkout extends Component{
    state={

        ingredients:null, 
         flag:false,
         totalPrice:0,

    }

    // componentWillMount(){
    //     const query=new URLSearchParams(this.props.location.search);
    //     const Newingredients={};
    //     let price=0;
    //     for(var params of query.entries()){

    //         if(params[0]==='Price'){
    //             price=params[1];
    //         }else{
    //             Newingredients[params[0]] = +params[1];

    //         }
    //         //    Newingredients[params[0]] = +params[1];
    //     }
    //     this.setState({ingredients:Newingredients,totalPrice:price})

    // }

    orderCancleHandler=()=>{
        // this.props.history.goBack();
        this.props.history.push('/');
    }

    orderContinueHandler=()=>{


        this.props.history.replace('/checkout')
         this.setState({flag:true})
    }



render(){

    let contactdata=''

    if(this.state.flag){
        
        contactdata=<Route path={'/checkout'} component={ContactDetails} />
        // console.log(contactdata)
    }

    let summary=<Redirect to='/'/>

    

    if(this.props.ings){
               
        let purchesRedirct= this.props.purches?<Redirect to='/'/>:null

        summary=(
            <div>
                {purchesRedirct}
            <CheckoutSummary 
            // ingredients={this.state.ingredients}
            ingredients={this.props.ings}
            orderCancle={this.orderCancleHandler}
            orderContinue={this.orderContinueHandler}
            
            />

        {contactdata}

        
            </div>
        )
      

    }

   

    return(
        <div> 
             {summary}   
        </div>
    )
}
}

const mapStateToProps=(state)=>{
    return{
        ings:state.BurgerReducer.ingredients,
        purches:state.orders.purches
    }

}




export default connect(mapStateToProps)(Checkout);


