import * as actionType from './action'
import axios from '../../axios';


 export const addIngredients=(name)=>{

    return{
        type:actionType.ADDINGREDIENT,
        ingredientName:name
    }
 }

 export const removeIngredients=(name)=>{
     return{
        type:actionType.REMOVEINGREDIENT,
        ingredientName:name
     }
     
 }

 export const failedTOFetchData=()=>{
     return{
         type:actionType.FAILEDTOFETCH
     }
 }


 export const addIngredientsthunk=(ingredients)=>{
    return{
        type:actionType.SETINGREDIENTS,
        ingredientsvar:ingredients
    }
 }


 export const setIngredients=()=>{
     return function(dispatch){

         axios.get("https://myapp-11582.firebaseio.com/ingredients.json").then(response=>{

                dispatch(addIngredientsthunk(response.data))

        }).catch(error=>{
            
            dispatch(failedTOFetchData())
        })

     }
 }

 export const purchesDone=()=>{
     return{
         
     }
 }




