import React from 'react';
import classes from './Burger.css';
import BurgerIngredient from '../Burger/BurgerIngredients/BurgerIngredients';

const Burger=(props)=>{

let ingredientArr=Object.keys(props.ingredients).map((igkey)=>{

    return [...Array(props.ingredients[igkey])].map((_,i)=>{
       return  <BurgerIngredient key={igkey+i} type={igkey}/>
    });

}).reduce((prevarr,currentele)=>{

    return prevarr.concat(currentele);
},[])

console.log(ingredientArr)

if(ingredientArr.length===0){

    ingredientArr=<p>Plz start adding ingredients.........</p>

}


    return(
        <div className={classes.Burger}>

        <BurgerIngredient type="bread-top"/>
        {/* <BurgerIngredient type="cheese"/>
        <BurgerIngredient type="meat"/> */}
          {ingredientArr}   

        <BurgerIngredient type="bread-bottom"/>
        </div>


    );


}

export default Burger;


