import React,{Component} from 'react';

import Aux from '../../hoc/Aux/Aux';
import Burger from '../../components/Burger/Burger';

import BuildControl from '../../components/Burger/BuildControls/BuildControls';

import Model from '../../components/UI/Model/Model';

import OrderSummary from '../../components/Burger/OrderSummary/OrderSummary';

// import axios from '../../axios';

import Spinner from '../../components/UI/Spinner/Spinner';
// import errorHandler from '../../hoc/ErrorHandler/ErrorHandler';

import {connect} from 'react-redux';

// import * as actionType from '../../store/action/action';

import * as action from '../../store/action/index';




 let ispriceChanges={
        flag:false
}


export class BurgerBuilder extends Component{
    state={
       Purchasable:false,
    //    OrderButton:false,
       loading:false,
    //    error:false,
    }
     

    componentDidMount(){
        console.log(this.props)
        this.props.onLoadIngredients()
      
    }

    // updatePurchasableState=(ingredientsVar)=>{

    //     const sum=Object.keys(ingredientsVar).map(igkey=>{

    //             return ingredientsVar[igkey]

    //     }).reduce((sum,ele)=>{
    //         return sum+ele;
    //     },0)

    //     return sum>0

    // }

   
    


orderButtonHandler=()=>{

if(this.props.isAuthenticated){
    this.setState({OrderButton:true})
}                
else{
    this.props.onSetRedirect("/checkout")
   this.props.history.push("/login");
}


}

orderCancled=()=>{

    this.setState({OrderButton:false})
}

orderConfirm=()=>{

    
this.props.onpurchesDone();
    this.props.history.push('/checkout')
}





    render(){

        // {this.props.priceFromreducer>10?ispriceChanges.flag=true:false}


        if(this.props.priceFromreducer>10){
            
            ispriceChanges.flag=true

        }else{
            ispriceChanges.flag=false
        }

        console.log("ispriceChange="+ispriceChanges.flag)


const disableInfo={
    // ...this.state.ingredients
    ...this.props.ing
}
    for(var key in disableInfo){

        disableInfo[key]=disableInfo[key]<=0;

    }

    let orderSummary=null
    let burger= this.props.error?<div><Spinner/><p> Not able to Load Data</p></div>:<Spinner/>



    

    if(this.props.ing !== null){
        burger=(
        <Aux>

                <Burger ingredients={this.props.ing}/>
                {/* <div>Burger Controls </div> */}

                <div>
                <BuildControl ingredientsAddControl={this.props.onAddIngredients}
                              ingredientsSubControl={this.props.onDeleteIngredients} 
                              disable={disableInfo}
                            //   price={this.state.totalPrice}
                              price={this.props.priceFromreducer}
                              orderButton={this.orderButtonHandler}
                            //   Purchasable={this.updatePurchasableState(this.props.ing)}/>
                                isAuth={this.props.isAuthenticated}
                              Purchasable={ispriceChanges.flag}
                              
                              />

                              </div>


        </Aux>
        );

        orderSummary=
                 <OrderSummary 
                        orderCancel={this.orderCancled} 
                        orderConfirm={this.orderConfirm} 
                        // totalPrice={this.state.totalPrice}
                        totalPrice={this.props.priceFromreducer}
                        ingredients={this.props.ing}  />


    }

    // if(this.state.loading===true){
    //     orderSummary=<Spinner/>
    // }

            return(
            <Aux>

                <Model showOrder={this.state.OrderButton} cancleOrder={this.orderCancled}>
                    {orderSummary}
                </Model>

                     {burger} 

                    {/* <Burger/> */}
                
            </Aux>
            );
    };
    

}

const mapStateToProps=(state)=>{
    return{
        ing:state.BurgerReducer.ingredients,
        priceFromreducer:state.BurgerReducer.totalPrice,
        error:state.BurgerReducer.error,
        isAuthenticated:state.auth.token,

        building:state.BurgerReducer.building


    }
}

const mapDispatchToProps=(dispatch)=>{
    return{
    onAddIngredients:(ingName)=>dispatch(action.addIngredients(ingName)),

    onDeleteIngredients:(ingName)=>dispatch(action.removeIngredients(ingName)),

    onLoadIngredients:()=>dispatch(action.setIngredients()),
    onpurchesDone:()=>dispatch(action.purchesDone()),
    onSetRedirect:(path)=>dispatch(action.setAuthRedirect(path))
    }
}



export default connect(mapStateToProps,mapDispatchToProps)(BurgerBuilder);

//export default errorHandler(BurgerBuilder,axios);







