import * as actionType from '../action/action'
import {updateObject} from '../utility'

const initialState={
    orders:[],
    loading:false,
    purches:false,
    flag:false,
    error:'',
    errorState:false,
}


console.log("orderinarray="+initialState.order)

const reducer=(oldstate=initialState,action)=>{
    
    switch(action.type){

        case (actionType.FETCH_DATA_START):
            return updateObject(oldstate,{loading:true})

            // return{
            //     ...oldstate,
            //     loading:true
            // }
        case (actionType.FETCH_DATA_SUCCESS):

            const updatedFetchData={
                orders:action.order,
                loading:false
            }

            return updateObject(oldstate,updatedFetchData)

            // return{
            //     ...oldstate,
            //     orders:action.order,
            //     loading:false
            // }
        case (actionType.FETCH_DATA_FAILED):


                return updateObject(oldstate,{loading:false})

            // return{
            //     ...oldstate,
            //     loading:false,
            //     error:action.error,
            //     errorState:true
            // }


        case (actionType.PURCHES_DONE):

            return updateObject(oldstate,{purches:false})
            // return{
            //     ...oldstate,
            //     purches:false,
                
            // }

        case (actionType.ORDER_PURCHES_START):

                return updateObject(oldstate,{loading:true})
        //     return{
        //         ...oldstate,
        //         loading:true,
                
        // };

        case (actionType.ORDERSUCCESS):
            const newOrder={
                ...action.orderData,
                id:action.orderId,
            }

            const updatedOrderSuccess={
                purches:true,
                orders:oldstate.orders.concat(newOrder),
            }

            return updateObject(oldstate,updatedOrderSuccess)

            // return{
            //     ...oldstate,
            //     purches:true,
            //     orders:oldstate.orders.concat(newOrder),
            // }
        case (actionType.ORDERFAILED):

            return updateObject(oldstate,{loading:false})
            // return{
            //     ...oldstate,
            //     loading:false

            // }   
        default :     
        return oldstate
    }

    
}


export default reducer


