import React from 'react';
import classes from './BuildControls.css'
import ButtonControls from './ButtonControls/ButtonControls';


 const controls=[
        {label:'Salad' , type:'salad'},
        {label:'Bacon' , type:'bacon'},
        {label:'Cheese' , type:'cheese'},
        {label:'Meat' , type:'meat'}

];

const buildControls=(props)=>(

    <div className={classes.BuildControl}>
        <div><h2>Price:{props.price.toFixed(2)}</h2></div>

        {controls.map(ctrl=>(
             <ButtonControls key={ctrl.label} 
                            label={ctrl.label} 
                           // type={ctrl.type}
                            added={()=>props.ingredientsAddControl(ctrl.type)}
                            remove={()=>props.ingredientsSubControl(ctrl.type)}
                            disable={props.disable[ctrl.type]}/>
        ))}
    <button className={classes.OrderButton} disabled={!props.Purchasable}
     onClick={props.orderButton}> {props.isAuth?'Order Now': 'SignIn TO Purches'}</button>
 
    </div>


);

export default buildControls;


