/* eslint-disable no-unused-expressions */
import React,{Component} from 'react';
import Button from '../../../components/UI/Button/Button';
import classes from './contactDetails.css'
// import axios from '../../../axios';
import Spinner from '../../../components/UI/Spinner/Spinner'
  import Input from '../../../components/UI/inputForm/input'

import {connect} from 'react-redux';

import * as actions from '../../../store/action/index'


class contactDetails extends Component{

       state = {
        orderForm: {
            name: {
                elementType: 'input',
                elementConfig: {
                    type: 'text',
                    placeholder: 'Your Name'
                },
                value: '',
                validation: {
                    required: true
                },
                valid: false,
                touched: false
            },
            street: {
                elementType: 'input',
                elementConfig: {
                    type: 'text',
                    placeholder: 'Street'
                },
                value: '',
                validation: {
                    required: true
                },
                valid: false,
                touched: false
            },
            zipCode: {
                elementType: 'input',
                elementConfig: {
                    type: 'text',
                    placeholder: 'ZIP Code'
                },
                value: '',
                validation: {
                    required: true,
                    minLength: 6,
                    maxLength: 6
                },
                valid: false,
                touched: false
            },
            country: {
                elementType: 'input',
                elementConfig: {
                    type: 'text',
                    placeholder: 'Country'
                },
                value: '',
                validation: {
                    required: true
                },
                valid: false,
                touched: false
            },
            email: {
                elementType: 'input',
                elementConfig: {
                    type: 'email',
                    placeholder: 'Your E-Mail'
                },
                value: '',
                validation: {
                    required: true
                },
                valid: false,
                touched: false
            },
            mode: {
                elementType: 'select',
                elementConfig: {
                    options: [
                        {value: 'fastest', displayValue: 'Fastest'},
                        {value: 'cheapest', displayValue: 'Cheapest'}
                    ],
                    
                },
                value: 'fastest',
                validation:{},
                valid:true
            }
        },
        formIsValid: false,
        loading: false,
        DateTime:''

    }

orderHandler=(event)=>{

        event.preventDefault();

        console.log(this.props.ings)
        // alert('Thank u :) ,Order is Confirmed!')
            let formData={};

            for(var orderElement in this.state.orderForm){
                formData[orderElement]=this.state.orderForm[orderElement].value
            }




        const date=new Date()
        let monthName=['jan','feb','march','april','may','jun','july','aug','sep','oct','nov','dec']
        let monthNumber=date.getMonth();
        monthName=monthName[monthNumber]

        let datevar=(date.getDate()+'/'+ monthName+'/'+date.getFullYear());
        let timevar=(date.getHours()%12+':'+date.getMinutes()+':'+date.getSeconds()); 
        
        let hour=date.getHours();

        let AMPM=hour>=12 ?'PM':'AM';

        let datetimevar=datevar+","+timevar+" "+AMPM;

    this.setState({loading:true,DateTime:datetimevar})

    const order={
        ingredients:this.props.ings,
        price:this.props.priceFromReducer,
        orderData:formData,
        orderDateTime:datetimevar,
        userId:this.props.userId
    }


    console.log("token from db="+ this.props.token)
    this.props.onOrderBurger(this.props.token,order);

  



    }
// input chnage handler

validationHandler=(value,rules)=>{
console.log("req="+value.required)
    let isValid = true;
        
                if (rules.required) {
                    isValid = value.trim() !== '' && isValid;
                }
        
                if (rules.minLength) {
                    isValid = value.length >= rules.minLength && isValid
                }
        
                if (rules.maxLength) {
                    isValid = value.length <= rules.maxLength && isValid
                }
        
                if(rules.isNumeric){

                    const pattern=/^\d+$/;
                    isValid=pattern.test(value) && isValid
                }

        return isValid;

}

    inputChangeHandler=(event,identifier)=>{
        // console.log(event.target.value)
        let updatedorderForm={
            ...this.state.orderForm
        };

        let updatedorderElement={
            ...updatedorderForm[identifier]
        }

        updatedorderElement.value=event.target.value;
        updatedorderElement.valid=this.validationHandler(updatedorderElement.value,updatedorderElement.validation)
        // updatedorderElement.touched=true;
        if(updatedorderElement.value==="" ){
            updatedorderElement.touched=true;
        }else{
            updatedorderElement.touched=false;
        }
        console.log(updatedorderElement)
        updatedorderForm[ identifier]=updatedorderElement;

            let formIsValid=true;

            for(let ele in updatedorderForm){
                formIsValid=updatedorderForm[ele].valid && formIsValid
            }
            console.log("formisValid="+formIsValid)

        this.setState({orderForm:updatedorderForm, formIsValid:formIsValid})

    }




    render(){
        let formElementArray=[];
        for(let key in this.state.orderForm){

            formElementArray.push({
                id:key,
                config:this.state.orderForm[key]
            });
        }
let form=''
if(this.props.loading===true){
    <Spinner> Loading... </Spinner>;
    
    <h2>Loading</h2>
}else{
    form=(
        <form onSubmit={this.orderHandler} >
        {/* <Input  ElementType='...' ElementConfig="..." value="..."/> */}
        {formElementArray.map(formElement=>{
            return (
                 <Input 
                        key={formElement.id}
                        ElementType={formElement.config.elementType}
                        ElementConfig={formElement.config.elementConfig}
                        value={formElement.config.value}
                        invalid={!formElement.config.valid}
                        shouldValidate={formElement.config.validation}
                        touched={formElement.config.touched}
                        changed={(event) => this.inputChangeHandler(event, formElement.id)} />

        
                    )        })}

        <Button btnType="Success" disabled={!this.state.formIsValid}>Order</Button>
        </form>
    
    );
}



        return(
            <div className={classes.contactDetails}>
                
                    <h3>Enter Your ContactDetails</h3>

                    {form}
            </div>
        )
        }

}


const mapStateTOProps=(state)=>{
    return{
        ings:state.BurgerReducer.ingredients,
        priceFromReducer:state.BurgerReducer.totalPrice,
        loading:state.orders.loading,
        token:state.auth.token,
        userId:state.auth.userid

    }
}


const mapDispatchToprops=(dispatch)=>{

    return{
        onOrderBurger:(token,order)=>dispatch(actions.orderPurches(token,order))

    }
}


export default connect(mapStateTOProps,mapDispatchToprops)(contactDetails);














